.modal-full {
  min-width: 100%;
  margin: 0;
  height: 100%;
  border: none;
  .modal-content {
    height: 100%;
    border: none;
    border-radius: 0;
  }
  .modal-body {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
  }
  .modal-header {
    padding: 2.5px;
    height: 30px;
    width: 30px;
    position: absolute;
    top: 0;
    z-index: 2;
    border-radius: 0;
    border-bottom-right-radius: 7px;
    background: #d6d6d6;
    box-shadow: 5px 5px 15px -9px rgba(0, 0, 0, 0.75);
    .close {
      padding: 0;
      margin: 0;
      height: 25px;
      width: 25px;
    }
  }
}

.modal {
  height: 100%;
}


$toolbarBG: #d6d6d6;
#da-toolbar {
  background: $toolbarBG;
  position: absolute;
  border-radius: 7px;
  box-shadow: 5px 5px 15px -9px rgba(0, 0, 0, 0.75);
  bottom: 15px;
  label {
    margin: 0;
    margin-left: 2px;
    margin-right: 2px;
    font-size: 0;
  }
  .toolbar-inner {
    display: flex;
    position: relative;
    width: auto;
    height: auto;
    align-items: center;
    justify-content: space-around;
    input {
      display: none;
    }
    .tool {
      width: auto;
      display: flex;
      height: auto;
      padding: 7px 5px;
      &:not(:last-child) {
        border-right: 2px solid darken($toolbarBG, 10);
      }
    }
    .tool-color {
      input:checked {
        + .color-indicator {
          border-color: transparent;
          opacity: 1;
        }
      }
      .color-indicator {
        display: inline-block;
        width: 20px;
        height: 20px;
        border: 3px solid $toolbarBG;
        border-radius: 50%;
        opacity: .7;
      }
      .tc-white .color-indicator {
        background: #ffffff;
      }
      .tc-black .color-indicator {
        background: #000000;
      }
      .tc-red .color-indicator {
        background: #ff0000;
      }
      .tc-green .color-indicator {
        background: #00ff00;
      }
      .tc-blue .color-indicator {
        background: #0000ff;
      }
    }
    .tool-line_width {
      input:checked {
        + .width-indicator {
          opacity: 1;
          background: #e9416d;
        }
      }
      .width-indicator {
        display: inline-block;
        width: 20px;
        height: 20px;
        border-color: $toolbarBG;
        border-style: solid;
        border-radius: 50%;
        opacity: .7;
        background: #000000;
      }
      .tlw-1 .width-indicator {
        border-width: 8px;
      }
      .tlw-3 .width-indicator {
        border-width: 6px;
      }
      .tlw-5 .width-indicator {
        border-width: 4px;
      }
      .tlw-7 .width-indicator {
        border-width: 2px;
      }
      .tlw-10 .width-indicator {
        border-width: 0px;
      }
    }
    .tool-line {
      .tool-indicator {
        display: block;
        position: relative;
        width: 20px;
        height: 20px;
        .line {
          display: block;
          position: absolute;
          width: 2px;
          height: 25px;
          transform: rotate(45deg);
          background: #000000;
          top: -2.5px;
          left: calc(50% - 1px);
        }
        &::before, &::after {
          display: block;
          content: '';
          position: absolute;
          height: 5px;
          width: 5px;
          background: #000000;
          border-radius: 50%;
        }
        &::before {
          left: 0;
          bottom: 0;
        }
        &::after {
          right: 0;
          top: 0;
        }
      }
      input:checked {
        + .tool-indicator {
          .line {
            background: #e9416d;
          }
          &::before, &::after {
            width: 6px;
            height: 6px;
            background: #e9416d;
          }
        }
      }
    }
    .tool-complete {
      svg {
        width: 20px;
        height: auto;
      }
    }
    .tool-clean {
      button {
        padding: 0;
        margin: 0;
      }
      svg {
        width: 20px;
        height: auto;
      }
    }
  }
}

@media (orientation: landscape) {
  #da-toolbar {
    bottom: unset;
    left: 15px;
    .toolbar-inner {
      flex-direction: column;
      .tool:not(:last-child) {
        border-right: unset;
        border-bottom: 2px solid darken($toolbarBG, 10);
      }
    }
    .tool {
      flex-direction: column;
    }
    label {
      margin: 0;
      margin-top: 2px;
      margin-bottom: 2px;
    }
  }
}

body {
  height: 100%;
}

input[accept*="camera"] {
  display: none;
}
